import './scss/settings-page.scss';
import template from './template/settings-page.html';
import SettingsCtrl from './SettingsCtrl';

angular.module('settings-page', [])
.component('settingsPage', {
    template,
    controller: ["$scope", "appSettings", SettingsCtrl],
    bindings: {},
});

export default 'settings-page';



