import uirouter from '@uirouter/angularjs';
import 'angular-spinner';
import Routes from '../routing/Routes';
import Settings from '../models/Settings';
import ColorUtils from '../utils/ColorUtils';
import TextColors from '../models/TextColors';
import ColorFactory from '../models/ColorFactory';
import ColorSetFactory from '../models/ColorSetFactory';
import RoutingCtrl from '../routing/RoutingCtrl';
import JSONrpc from '../utils/JsonRpc';
import main from "../pages/main/main-page";
import settings from "../pages/settings/settings-page";
import guide from "../pages/guide/guide-page";
import './scss/color-search.scss';
import template from './template/color-search.html';
import Login from '../models/Login';
import popupDialog from '../services/dialog/dialog';
import db from '../services/db/db';
import eventFilter from '../utils/EventFilter';

angular.module('color-search', [uirouter, JSONrpc, main, settings, guide, popupDialog, db, eventFilter, 'angularSpinner'])
.config(Routes)
.config(['$JsonRpcProvider', (provider) => {
    provider.set({ url: "/" });
}])
.service('colorUtils', ColorUtils)
.service('colorFactory', ColorFactory)
.service('colorSetFactory', ColorSetFactory)
.service('appSettings', ['db', Settings])
.service('textColors', TextColors)
.service('login', ['$q', 'jsonrpc', Login])
.controller('RoutingCtrl', RoutingCtrl)
.component('colorSearch', {
    template,
    controller: ['$scope', '$window', 'login', 'popupDialog',
    function Controller($scope, $window, login, dialog) {
        function openLoginDialog() {
            dialog.loginDialog(function () {
                this.model = login;
                this.login = () => {
                    if (this.model.isSet()) {
                        this.model.login()
                        .then((succ) => {
                            if (succ) {
                                dialog.close();
                            }
                        });
                    }
                };
            });
        }

        login.isRequired()
        .then((required) => {
            if (required) {
                openLoginDialog();
            }
        }).catch((err) => {
            dialog.errorDialog(function () {
                this.title = "Connection error";
                this.description = "Try to reload the application.";
                this.buttonTitle = "Reload";
                this.button = true;
                this.icon = 'refresh';
                this.handle = () => {
                    $window.location.reload(true);
                };
            });
        });
    }],
});

export default 'color-search';



