export default class MainCtrl {

    constructor($rootScope, $scope, textColors,
        $timeout, appSettings, colorSetFactory, colorFactory, dialog) {
        this.appSettings = appSettings;
        this.colors = new Map();
        this.textColors = textColors;
        this.selectedWord = {};
        this.$scope = $scope;
        this.colorSetFactory = colorSetFactory;
        this.colorFactory = colorFactory;
        this.dialog = dialog;
    }

    _updateColors() {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            const cols = colorizer.getAllColors();
            this.textColors.clearWords();
            this.colors.clear();
            for (const value of cols.values()) {
                const word = value.word.toLowerCase();
                const color = value.color;
                if (color.isSet()) {
                    this.textColors.addWord(word);
                    const entry = this.colors.get(word);
                    if (entry) {
                        entry.add(color);
                    } else {
                        const colorSet = this.colorSetFactory.getInstance();
                        colorSet.add(color);
                        this.colors.set(word, colorSet);
                    }
                }
            }
        });
    }

    loadColorProfile(colors) {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            colorizer.setColors(colors, () => {
                this._updateColors();
                this.textColors.updateColors(colorizer.getAllColors());
                this.$scope.$broadcast("colorsChainged");
            });
        });
    }

    selectWord(word) {
        this.$scope.$broadcast("color-toolbar", (toolbar) => {
            this.selectedWord = word;
            toolbar.setColor(word.color);
            toolbar.open();
        });
    }

    colorModifiedHandler(color) {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            if (color && colorizer) {
                if (color.isSet()) {
                    colorizer.setColor(this.selectedWord, color, () => {
                        this.textColors.updateColors(colorizer.getAllColors());
                        this.$scope.$broadcast("colorsChainged");
                    });
                } else {
                    colorizer.resetColor(this.selectedWord, () => {
                        this.textColors.updateColors(colorizer.getAllColors());
                        this.$scope.$broadcast("colorsChainged");
                    });
                }
                this._updateColors();
            }
        });
    }

    inputComplete() {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            colorizer.inputComplete(() => {
                this._updateColors();
                this.textColors.updateColors(colorizer.getAllColors());
                this.$scope.$broadcast("colorsChainged");
            });
        });
    }

    drop() {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            colorizer.clear(() => {
                this._updateColors();
                this.textColors.clear();
                this.$scope.$broadcast("colorsChainged");
            });
        });
    }

    dropDialog() {
        const self = this;
        this.dialog.confirmDialog(function () {
            this.yes = () => {
                self.drop();
                self.dialog.close();
            };
            this.cancel = () => {
                self.dialog.close();
            };
            this.title = '"drop"';
        });
    }

    decolorizeDialog() {
        const self = this;
        this.dialog.confirmDialog(function () {
            this.yes = () => {
                self.decolorizeText();
                self.dialog.close();
            };
            this.cancel = () => {
                self.dialog.close();
            };
            this.title = '"clear"';
        });
    }

    decolorizeText() {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            colorizer.resetAllColors(() => {
                this._updateColors();
                this.textColors.clear();
                this.$scope.$broadcast("colorsChainged");
            });
        });
    }

    edit() {
        this.$scope.$broadcast("text-colorizer", (colorizer) => {
            colorizer.editMode();
        });
    }

    search() {
        this.$scope.$broadcast("image-finder", (imageFinder) => {
            const colorsAndWords = this.textColors.getColorsAndWords();
            if (!this.appSettings.checkSemanticSimilarity) {
                colorsAndWords.words = [];
            }
            imageFinder.search(colorsAndWords, this.appSettings.getSearchSettings());
        });
    }
}



