class ColorSet {

    constructor() {
        this.map = new Map();
        this[Symbol.iterator] = this.values;
    }

    add(color) {
        this.map.set(color.toIdString(), color);
    }

    values() {
        return this.map.values();
    }

    getValuesArray() {
        return [...this.map.values()];
    }
}

export default ColorSet;




