import tc from 'tinycolor2';

export default class ColorUtils {

    hslToHexString(color, f) {
        const factor = f || 1;
        return tc({ h: color.h, s: color.s / factor, l: color.l / factor }).toHexString();
    }

    hexToHsl(hex, f) {
        const factor = f || 1;
        const hsl = tc(hex).toHsl();
        hsl.s = Math.round(hsl.s * factor);
        hsl.l = Math.round(hsl.l * factor);
        return hsl;
    }

    toHexString(string) {
        return tc(string).toHexString();
    }

    hslToHslString(color) {
        return `hsl(${color.h},${color.s}%,${color.l}%)`;
    }

    hslStringToHsl(string, f) {
        if (string) {
            const factor = f || 1;
            const hsl = tc(string).toHsl();
            hsl.s = Math.round(hsl.s * factor);
            hsl.l = Math.round(hsl.l * factor);
            return hsl;
        }
        return { h: 0, s: 0, l: 0 };
    }

    getFittingColorWithGreaterCR(color, ...colors) {
        let cTemp = color;
        if (colors && colors.length > 0) {
            cTemp = colors[0];
            let crTemp = this.cr(color, colors[0]);
            for (let i = 0; i < colors.length; i++) {
                const cr = this.cr(color, colors[i]);
                if (crTemp < cr) {
                    crTemp = cr;
                    cTemp = colors[i];
                }
            }
        }
        return cTemp;
    }

    // W3C recommendation
    cr(c1, c2) {
        const color1 = tc(c1).toRgb();
        const color2 = tc(c2).toRgb();
        let lum1 = this._luminanceRGB(color1.r, color1.g, color1.b);
        let lum2 = this._luminanceRGB(color2.r, color2.g, color2.b);
        if (lum2 > lum1) {
            [lum1, lum2] = [lum2, lum1];
        }
        return (lum1 + 0.05) / (lum2 + 0.05);
    }

    _luminanceRGB(r, g, b) {
        const RsRGB = r / 255;
        const GsRGB = g / 255;
        const BsRGB = b / 255;
        const R = (RsRGB <= 0.03928) ? RsRGB / 12.92 : Math.pow(((RsRGB + 0.055) / 1.055), 2.4);
        const G = (GsRGB <= 0.03928) ? GsRGB / 12.92 : Math.pow(((GsRGB + 0.055) / 1.055), 2.4);
        const B = (BsRGB <= 0.03928) ? BsRGB / 12.92 : Math.pow(((BsRGB + 0.055) / 1.055), 2.4);
        return 0.2126 * R + 0.7152 * G + 0.0722 * B;
    }
}




