import Color from './Color';

export default class TextColors {

    constructor() {
        this._words = [];
        this.colorOccurence = new Map();
        this.adjustedColorOccurence = new Map();
        this.sPercentage = 100;
        this.lPercentage = 100;
    }

    clearWords() {
        this._words = [];
    }

    addWord(word) {
        this._words.push(word);
    }

    getWords() {
        return this._words;
    }

    getColors() {
        return [...this.adjustedColorOccurence.values()];
    }

    getColorsAndWords() {
        const o = {};
        o.colors = this.getColors();
        o.words = this.getWords();
        return o;
    }

    clear() {
        this.clearWords();
        this.colorOccurence.clear();
        this.adjustedColorOccurence.clear();
    }

    updateColors(colors) {
        this.colorOccurence.clear();
        if (colors && colors.size > 0) {
            for (const [key, value] of colors.entries()) {
                if (value.color && value.color.isSet()) {
                    let color = this.colorOccurence.get(value.color.toString());
                    if (color) {
                        color.setWeight(color.weight + value.color.weight);
                    } else {
                        color = new Color(value.color);
                        this.colorOccurence.set(color.toString(), color);
                    }
                }
            }
        }
        this.calculateAdjustedColors();
    }

    // from -100 to +100
    calculateAdjustedColors() {
        this.adjustedColorOccurence.clear();
        for (const value of this.colorOccurence.values()) {
            const s = this._calculate(value.s, this.sPercentage);
            const l = this._calculate(value.l, this.lPercentage);
            const color = new Color({ h: value.h, s, l, weight: value.weight });
            if (value.isAchromatic()) {
                color.setAchromatic();
            }
            this.adjustedColorOccurence.set(color.toString(), color);
        }
    }

    // Lagrange interpolation
    _calculate(x, xPercentage) {
        const x0 = 100;
        const x1 = (xPercentage > 100) ? 200 : 0;
        const y0 = x;
        const y1 = (xPercentage > 100) ? 100 : 0;
        return y0 * ((xPercentage - x1) / (x0 - x1)) + y1 * ((xPercentage - x0) / (x1 - x0));
    }
}



