class EventFilter {

    constructor(timeWindow, callback, $window) {
        this._timeWindow = timeWindow;
        this._callback = callback;
        this._$window = $window;
        this._lastEventTime = 0;
        this._timeoutId = null;
    }

    event() {
        const now = Date.now();
        if ((now - this._lastEventTime) >= this._timeWindow) {
            if (this._timeoutId) {
                this._$window.clearTimeout(this._timeoutId);
            }
            this._lastEventTime = now;
            this._callback();
        } else {
            if (this._timeoutId) {
                this._$window.clearTimeout(this._timeoutId);
            }
            this._timeoutId = this._$window.setTimeout(() => {
                this._lastEventTime = now;
                this._callback();
            }, this._timeWindow);
        }
    }
}

angular.module("event-filter", [])
.factory("eventFilter", ["$window", function ($window) {
    return (timeWindow, callback) => new EventFilter(timeWindow, callback, $window);
}]);

export default "event-filter";




