import './scss/color-profiles.scss';
import template from './template/color-profiles.html';

angular.module('color-profiles', [])
.service('colorProfiles', ['db', 'colorFactory', function (db, colorFactory) {
    return new ColorProfiles(new ColorProfilesDAO(db, colorFactory));
}])
.component('colorProfiles', {
    template,
    controller: ['$rootScope', '$scope', '$timeout', 'colorProfiles', 'colorFactory', 'colorSetFactory', 'popupDialog',
    function Controller($rootScope, $scope, $timeout, colorProfiles, colorFactory, colorSetFactory, dialog) {
        this.createProfile = false;
        this.notDeleteColors = false;
        this.newProfileName = '';
        this.colorProfiles = colorProfiles;
        this.profiles = [];
        this.conflictColors = [];
        let singleColors = [];
        let mode = '';

        this.updateProfileDialog = () => {
            const self = this;
            dialog.confirmDialog(function () {
                this.yes = () => {
                    self.updateProfile();
                    dialog.close();
                };
                this.cancel = () => {
                    dialog.close();
                };
                this.title = '"update profile"';
            });
        };

        this.applyProfileDialog = () => {
            const self = this;
            dialog.confirmDialog(function () {
                this.yes = () => {
                    self.applyProfile();
                    dialog.close();
                };
                this.cancel = () => {
                    dialog.close();
                };
                this.title = '"apply profile"';
            });
        };

        this.deleteProfileDialog = () => {
            const self = this;
            dialog.confirmDialog(function () {
                this.yes = () => {
                    self.deleteProfile();
                    dialog.close();
                };
                this.cancel = () => {
                    dialog.close();
                };
                this.title = '"delete profile"';
            });
        };

        this.toggleNewProfile = () => {
            this.createProfile = !this.createProfile;
        };

        this.haveProfiles = () => this.profiles.length > 0;

        this.selectProfile = () => {
            const name = colorProfiles.selectedProfile.name;
            if (name !== null && name !== undefined) {
                this._resetColorConflicts();
                colorProfiles.loadColorProfile(name, () => {});
            }
        };

        this.saveProfile = () => {
            const name = this.newProfileName;
            if (name !== undefined && name !== null && name.length > 0) {
                this._resetColorConflicts();
                mode = 'create';
                this._testForColorConflicts();
                if (this.conflictColors.length < 1) {
                    const colors = this._convertColorArrayToMap(singleColors);
                    this._createColorProfile(name, colors);
                }
            }
        };

        this.applyProfile = () => {
            const name = colorProfiles.selectedProfile.name;
            if (name !== null && name !== undefined) {
                this._resetColorConflicts();
                this.onLoadProfile({
                    colors: colorProfiles.getSelectedProfileColors(),
                });
            }
        };

        this.updateProfile = () => {
            const name = colorProfiles.selectedProfile.name;
            if (name !== null && name !== undefined) {
                this._resetColorConflicts();
                mode = 'update';
                const colors = colorProfiles.getSelectedProfileColors();

                if (!this.notDeleteColors) {
                    for (const word of colors.keys()) {
                        if (!this.colors.has(word)) {
                            colors.delete(word);
                        }
                    }
                }

                this._testForColorConflicts(colors);
                if (this.conflictColors.length < 1) {
                    const cMap = this._convertColorArrayToMap(singleColors);
                    this._updateColorProfile(name, cMap);
                }
            }
        };

        this.deleteProfile = () => {
            const name = colorProfiles.selectedProfile.name;
            if (name !== null && name !== undefined) {
                colorProfiles.deleteColorProfile(name, () => {
                    $scope.$apply();
                    const profileName = this.profiles[this.profiles.length - 1];
                    this._resetColorConflicts();
                    colorProfiles.loadColorProfile(profileName, () => {
                        $scope.$apply();
                    });
                });
            }
        };

        this.solveColorConflicts = () => {
            const colors = this._concatColorArraysToMap();
            switch (mode) {
                case 'update': {
                    const name = colorProfiles.selectedProfile.name;
                    if (name !== null && name !== undefined) {
                        this._updateColorProfile(name, colors);
                    }
                } break;
                case 'create': {
                    const name = this.newProfileName;
                    if (name !== null && name !== undefined) {
                        this._createColorProfile(name, colors);
                    }
                } break;
            }
        };

        // helper functions
        this._resetColorConflicts = () => {
            this.conflictColors = [];
            singleColors = [];
            mode = '';
        };

        this._createColorProfile = (name, colors) => {
            colorProfiles.createColorProfile(name, colors, () => {
            this.newProfileName = "";
            this._resetColorConflicts();
            colorProfiles.loadColorProfile(name, () => {
                $scope.$apply();
                });
            });
        };

        this._updateColorProfile = (name, colors) => {
            colorProfiles.updateColorProfile(name, colors, () => {
                this._resetColorConflicts();
                $timeout(() => {
                    $scope.$apply();
                }, 0);
            });
        };

        this._testForColorConflicts = (colors) => {
            const colorsMapTemp = new Map();
            for (const [word, mapColors] of this.colors) {
                const colorsArray = mapColors.getValuesArray();
                const colorSet = colorSetFactory.getInstance();
                for (let i = 0; i < colorsArray.length; i++) {
                    colorSet.add(colorsArray[i]);
                }
                colorsMapTemp.set(word, colorSet);
            }

            if (colors) {
                for (const [word, color] of colors) {
                    const c = colorFactory.getInstance(color);
                    const entry = colorsMapTemp.get(word);
                    if (entry) {
                        entry.add(c);
                    } else {
                        const colorSet = colorSetFactory.getInstance();
                        colorSet.add(c);
                        colorsMapTemp.set(word, colorSet);
                    }
                }
            }

            for (const [word, mapColors] of colorsMapTemp) {
                const colorsToWordArray = mapColors.getValuesArray();
                if (colorsToWordArray.length > 1) {
                    const sel = colorsToWordArray[0];
                    this.conflictColors.push({ word, selected: sel, colors: colorsToWordArray });
                } else {
                    const color = colorsToWordArray[0];
                    singleColors.push({ word, color });
                }
            }
        };

        this._concatColorArraysToMap = () => {
            let colors = [];
            this.conflictColors.forEach((item) => {
                colors.push({ word: item.word, color: item.selected });
            });
            colors = colors.concat(singleColors);
            const cMap = this._convertColorArrayToMap(colors);
            return cMap;
        };

        this._convertColorArrayToMap = (arr) => {
            const colorMap = new Map();
            for (let i = 0, k = arr.length; i < k; i++) {
                const item = arr[i];
                colorMap.set(item.word, item.color);
            }
            return colorMap;
        };

        this.$onInit = () => {
            // bind model and viewmodel
            colorProfiles.load(() => {
                this.profiles = colorProfiles.getColorProfileNames();
                if (this.profiles && this.profiles.length > 0) {
                    const name = this.profiles[this.profiles.length - 1];
                    colorProfiles.loadColorProfile(name, () => {
                        $scope.$apply();
                    });
                }
            });
        };
    }],

    bindings: {
        colors: '<',
        onLoadProfile: '&',
    },
});


class ColorProfiles {

    constructor(dao) {
        this.profileNames = [];
        this.selectedProfile = {};
        this.dao = dao;
        this.dao.setProfiles(this);
    }

    loadColorProfile(name, callback) {
        this.dao.loadColorProfile(name, callback);
    }

    // get profiles
    load(callback) {
        this.dao.loadColorProfiles(callback);
    }

    createColorProfile(name, colors, callback) {
        const profile = new ColorProfile(name, colors);
        this.dao.saveColorProfile(profile, callback);
    }

    deleteColorProfile(name, callback) {
        this.dao.deleteColorProfile(name, callback);
    }

    getColorProfileNames() {
        return this.profileNames;
    }

    getSelectedProfileColors() {
        return this.selectedProfile.colors;
    }

    updateColorProfile(name, colors, callback) {
        if (this.selectedProfile.name === name) {
            const profile = new ColorProfile(name, colors);
            this.dao.updateColorProfile(profile, callback);
        }
    }

    _removeColorProfileName(name) {
        let index;
        for (let i = 0; i < this.profileNames.length; i++) {
            if (this.profileNames[i] === name) {
                index = i;
                break;
            }
        }
        this.profileNames.splice(index, 1);
    }

    _setColorProfile(profile) {
        this.selectedProfile = profile;
    }

    _addColorProfileName(name) {
        this.profileNames.push(name);
    }
}

class ColorProfile {
    constructor(name, colors) {
        this.name = name;
        this.colors = colors;
    }

    static deserialize(o, colorFactory) {
        const name = o.name;
        const colors = new Map(o.colors);
        for (const [word, color] of colors) {
            colors.set(word, colorFactory.getInstance(color));
        }
        return new ColorProfile(name, colors);
    }

    serialize() {
        const self = this;
        const o = {};
        o.name = this.name;
        o.colors = [...self.colors];
        return o;
    }
}

class ColorProfilesDAO {

    constructor(db, colorFactory) {
        this.colorFactory = colorFactory;
        this.profiles = [];
        this.store = 'colorprofiles';
        this.db = db;
    }

    setProfiles(profiles) {
        this.profiles = profiles;
    }

    loadColorProfile(name, callback) {
        this.db.getStore(this.store)
        .getItem(name)
        .then((item) => {
            if (item) {
                const profile = ColorProfile.deserialize(item, this.colorFactory);
                this.profiles._setColorProfile(profile);
            }
            if (callback) {
                callback();
            }
        })
        .catch(() => {
            console.warn("Database error");
        });
    }

    deleteColorProfile(name, callback) {
        this.db.getStore(this.store)
        .removeItem(name)
        .then(() => {
            this.profiles._removeColorProfileName(name);
            if (callback) {
                callback();
            }
        })
        .catch(() => {
            console.warn("Database error");
        });
    }

    saveColorProfile(profile, callback) {
        const store = this.db.getStore(this.store);
        store.getItem(profile.name)
        .then((item) => {
            if (!item) {
                return store.setItem(profile.name, profile.serialize())
                .then(() => {
                    this.profiles._addColorProfileName(profile.name);
                    this.profiles._setColorProfile(profile);
                    if (callback) {
                        callback();
                    }
                });
            }
            return null;
        })
        .catch((e) => {
            console.warn("Database error");
            console.error(e);
        });
    }

    loadColorProfiles(callback) {
        this.db.getStore(this.store)
        .iterate((value, key) =>
            this.profiles._addColorProfileName(key))
        .then(() => {
            if (callback) {
                callback();
            }
        })
        .catch(() => {
            console.warn("Database error");
        });
    }

    updateColorProfile(profile, callback) {
        this.db.getStore(this.store)
        .setItem(profile.name, profile.serialize())
        .then(() => {
            this.loadColorProfile(profile.name, () => {
                if (callback) {
                    callback();
                }
            });
        })
        .catch(() => {
            console.warn("Database connection error");
        });
    }
}

export default 'color-profiles';



