import './scss/main-page.scss';
import template from './template/main-page.html';
import MainCtrl from './MainCtrl';
import colorAdjuster from '../../components/color-adjuster/color-adjuster';
import colorProfiles from '../../components/color-profiles/color-profiles';
import textColorizer from '../../components/text-colorizer/text-colorizer';
import toolbar from '../../components/color-toolbar/color-toolbar';
import imageFinder from '../../components/image-finder/image-finder';

angular.module('main-page', [colorAdjuster, colorProfiles, textColorizer, toolbar, imageFinder])
.factory('calculateFontColor', ['colorUtils', colorUtils =>
    color => colorUtils.getFittingColorWithGreaterCR(color, "#000000", "#ffffff")])
.component('mainPage', {
    template,
    controller: ['$rootScope', '$scope', 'textColors',
    '$timeout', 'appSettings', 'colorSetFactory', 'colorFactory', 'popupDialog',
    MainCtrl],
});

export default 'main-page';



