class RoutingCtrl {
    constructor($scope, $transitions, $state, $timeout) {
        $scope.page = "main";
        // router event
        $transitions.onSuccess({}, (transition) => {
            const data = transition.to().data;
            $scope.page = data["page"];
            $timeout(() => {
                $scope.$broadcast('rzSliderForceRender');
            }, 0);
        });

        $scope.goToSettings = () => {
            this.page = "settings";
            $state.go('main.settings');
        };

        $scope.goToGuide = () => {
            this.page = "guide";
            $state.go('main.guide');
        };

        $scope.goToMain = () => {
            this.page = "main";
            $state.go('main');
        };
    }
}

RoutingCtrl.$inject = ['$scope', '$transitions', '$state', '$timeout'];
export default RoutingCtrl;



