import md5 from 'md5';

export default class Login {

    constructor($q, jsonrpc) {
        this.required = null;
        this.auth = null;
        this.success = false;
        this.user = null;
        this.password = null;
        this.jsonrpc = jsonrpc;
        this.$q = $q;
    }

    isSet() {
        return this.user && this.password;
    }

    isSuccessful() {
        return this.auth !== null && this.success;
    }

    login() {
        const self = this;
        const auth = md5(`${this.user}${this.password}`);
        return this.jsonrpc.request('login', { login: auth })
        .then((data) => {
            if (data.success) {
                self.auth = auth;
                this.success = true;
                return true;
            }
            return false;
        });
    }

    isRequired() {
        if (this.required) {
            return this.$q.resolve(true);
        }
        return this.jsonrpc.request('login-required', {})
        .then((data) => {
            this.required = data.required;
            return this.required;
        });
    }
}



