import scroll from 'ng-infinite-scroll';
import 'ng-error';
import './scss/image-finder.scss';
import template from './template/image-finder.html';

angular.module('image-finder', ['jsonrpc', scroll, 'ngError'])
.service('images', ['jsonrpc', 'login', function (jsonrpc, login) {
    return new Images(new ImagesService(jsonrpc, login));
}])
.component('imageFinder', {
    template,
    controller: ['$scope', '$element', 'images', '$window', 'usSpinnerService',
    function Controller($scope, $element, images, $window, usSpinnerService) {
        const step = 10;
        const finder = this;
        this.selectedImage = {};
        this.imagesToShow = [];
        this.showImage = false;

        this.$onInit = function () {
            // restore view state
            this.imagesToShow = images.getImagesInInterval(0, images.showIndex);
        };

        const api = {
            search: (data, searchSettings) => {
                if (data && data.colors && data.colors.length > 0) {
                    usSpinnerService.spin('spinner-1');
                    images.findImages(data, searchSettings, () => {
                        usSpinnerService.stop('spinner-1');
                        finder.imagesToShow = images.getImagesInInterval(0, step);
                        images.showIndex = step;
                    });
                }
            },
            getImages: () => images.getImagesArray(),
        };

        $scope.$on("image-finder", (event, callback) => {
            if (typeof callback === "function") {
                callback(api);
            }
        });

        this.imageLoadingError = () => {
            this.showImage = false;
        };

        this.thumbnailLoadingError = ($event) => {
            angular.element($event.target.parentNode).addClass("hidden");
            this.showMore();
        };

        this.originalURL = () => {
            $window.open(this.selectedImage.originalURL);
        };

        this.showMore = () => {
            this.imagesToShow = this.imagesToShow
            .concat(images.getImagesInInterval(images.showIndex, images.showIndex + step));
            images.showIndex += step;
        };

        this.selectImage = ($event) => {
            const target = $event.target;
            if (target.hasAttribute('data-imagefinder-image') || angular.element(target).parent()[0].hasAttribute('data-imagefinder-image')) {
                $event.stopPropagation();
                const index = (target.hasAttribute('data-imagefinder-image')) ? parseInt(angular.element(target).attr('data-imagefinder-image')) : parseInt(angular.element(target).parent().attr('data-imagefinder-image'));
                this.selectedImage = images.getImage(index);
                if (this.selectedImage) {
                    angular.element('body').addClass('stop-scrolling');
                    this.showImage = true;
                }
            }
        };

        this.closeImagePopup = function ($event) {
            const target = $event.target;
            if (target.hasAttribute('data-imagefinder-close')) {
                $event.stopPropagation();
                angular.element('body').removeClass('stop-scrolling');
                this.showImage = false;
            }
        };
    }],
});

class Image {

    constructor(url, originalURL, score) {
        this.url = url;
        this.originalURL = originalURL;
        this.score = score;
    }
}

class Images {

    constructor(service) {
        this.images = [];
        this.service = service;
        this.showIndex = 0;
    }

    addImage(url, originalURL, score) {
        const img = new Image(url, originalURL, score);
        this.images.push(img);
    }

    getImage(i) {
        return this.images[i];
    }

    getImagesArray() {
        return this.images;
    }

    getImagesInInterval(start, end) {
        if (start >= 0 && start < this.images.length) {
            if (end > this.images.length) {
                end = this.images.length;
            }
            return this.images.slice(start, end);
        }
        return [];
    }

    findImages(data, searchSettings, callback) {
        this.images.length = 0;
        this.service.findImages(this, data, searchSettings, callback);
    }
}

class ImagesService {

    constructor(jsonrpc, login) {
        this.jsonrpc = jsonrpc;
        this.login = login;
    }

    findImages(images, data, searchSettings, callback) {
        const self = this;
        this.jsonrpc.request('find', { colors: data.colors, words: data.words, settings: searchSettings, auth: self.login.auth })
        .then((result) => {
            if (result && result.imgs && result.imgs.length > 0) {
                for (let i = 0; i < result.imgs.length; i++) {
                    const iObj = result.imgs[i];
                    images.addImage(iObj.url, iObj.originalURL, parseFloat(iObj.score).toFixed(2));
                }
            }
            result = null;
            if (callback) {
                callback();
            }
        })
        .catch((error) => {
            console.error(error);
            if (callback) {
                callback();
            }
        });
    }
}

export default 'image-finder';



