import XRegExp from 'xregexp';
import Entities from 'html-entities';
import './scss/text-colorizer.scss';
import template from './template/text-colorizer.html';

const entities = Entities.AllHtmlEntities;
angular.module('text-colorizer', [])
.component('textColorizer', {
    template,
    controller: ["$scope", "$element", "$attrs", "$timeout", "$window", "calculateFontColor", "colorFactory",
    function ($scope, $element, $attrs, $timeout, $window, calculateFontColor, colorFactory) {
        const _colorizer = this;
        const calcFontColor = calculateFontColor;
        const _options = {
            widgetSelector: 'data-textcolorizer-text',
            wordSelector: 'data-textcolorizer-word',
            colorSelector: 'data-textcolorizer-color',
            weightSelector: 'data-textcolorizer-weight',
            helperSelector: 'data-textcolorizer-helper',
        };

        const _vars = {
            root: angular.element($element.find(`div[${_options.widgetSelector}]`)[0]),
            words: new Map(),
            wordsToRemove: new Set(),
            editMode: false,
            idCounter: 0,
            helperCounter: 0,
        };

        const specialChars = new Map();
        specialChars.set('#%%%#;', '<');
        specialChars.set('#%%#%;', '>');
        specialChars.set('#%#%%;', '</');
        specialChars.set('##%%%;', '"');

        function encodeSpecialChars(str) {
            return str.replace(/#%%%#;|#%%#%;|#%#%%;|##%%%;/g, m => specialChars.get(m));
        }

        // public api
        const api = {
            inputComplete(callback) {
                _colorizer.inputComplete(callback);
            },
            editMode() {
                _colorizer.editMode();
            },
            setColor(colorObject, conf, callback) {
                _colorizer.setColor(colorObject, conf, callback);
            },
            resetColor(colorObject, callback) {
                _colorizer.resetColor(colorObject, callback);
            },
            resetAllColors(callback) {
                _colorizer.resetAllColors(callback);
            },
            getAllColors() {
                return _colorizer.getAllColors();
            },
            setColors(colors, callback) {
                _colorizer.setColors(colors, callback);
            },
            clear(callback) {
                _colorizer.clear(callback);
            },
        };

        $scope.$on("text-colorizer", (event, callback) => {
            if (typeof callback === "function") {
                callback(api);
            }
        });

        // allows only plain text paste
        this._handlePaste = (e) => {
            e.preventDefault();
            e.stopPropagation();
            let text = '';
            if (e.clipboardData || e.originalEvent.clipboardData) {
                text = (e.originalEvent || e).clipboardData.getData('Text');
            } else if ($window.clipboardData) {
                text = $window.clipboardData.getData('Text');
            }
            if ($window.document.queryCommandSupported('insertText')) {
                $window.document.execCommand('insertText', false, text);
            } else {
                $window.document.execCommand('paste', false, text);
            }
        };

        // Chrome workaround for proper style and behavior
        // handler for "keydown" events in widget
        this._handleKeydown = (event) => {
            // update & delete word nodes if enter or delete keyevents performed
            if (event.keyCode === 13 || event.keyCode === 8 || event.keyCode === 46) {
                $window.setTimeout(() => {
                    _colorizer._deleteEmpty();
                    _colorizer._updateFontStyle();
                }, 0);
            }

            if (event.keyCode === 8 || event.keyCode === 46) {
                if ($window && $window.getSelection && $window.getSelection().anchorNode && $window.getSelection().getRangeAt(0)) {
                    const target = $window.getSelection().anchorNode.parentNode;
                    if (angular.element(target).attr(`${_options.wordSelector}`)) {
                        const sel = $window.getSelection();
                        // if different nodes are involved
                        // check all nodes
                        if (sel.anchorNode === sel.focusNode) {
                            const range = sel.getRangeAt(0);
                            const startIndex = range.startOffset;
                            const endIndex = range.endOffset;
                            const count = Math.abs(startIndex - endIndex);
                            const text = sel.anchorNode.nodeValue;

                            if (count < 2) {
                                if (event.keyCode === 8) {
                                    if (startIndex === 1 || startIndex === 0 || endIndex === 1 || endIndex === 0) {
                                        if (text.trim().length > 0) {
                                                event.preventDefault();
                                        }
                                    } else if (startIndex === text.length || endIndex === text.length) { 
                                        event.preventDefault();
                                        sel.anchorNode.nodeValue = `${text.slice(0, -2)}\u00A0`;
                                        this._moveCaretInText(sel.anchorNode.nodeValue.length, sel.anchorNode);
                                    }
                                } else if (event.keyCode === 46) {
                                    if (startIndex === 0 || startIndex === text.length || endIndex === 0 || endIndex === text.length ){
                                        event.preventDefault();
                                    }
                                }
                            }

                            if (count > 1 && count < text.length) {
                                if (startIndex === 0) {
                                    event.preventDefault();
                                    sel.anchorNode.nodeValue = `\u00A0${text.slice(endIndex)}`;
                                    this._moveCaretInText(1, sel.anchorNode);
                                } else if (endIndex === text.length) {
                                    event.preventDefault();
                                    sel.anchorNode.nodeValue = `${text.slice(0, -count)}\u00A0`;
                                    this._moveCaretInText(sel.anchorNode.nodeValue.length, sel.anchorNode);
                                }
                            }
                        }
                    }
                }
            }
        };

        // Chrome fonts
        this._updateFontStyle = () => {
            angular.element(_vars.root).css({
                'font-size': '1.2em',
                'line-height': '1.7em',
            });
        };

        this._handleCut = () => {
            $window.setTimeout(() => colorizer._updateWordsStyle(), 0);
        };

        this._updateWordsStyle = () => {
            angular.element(_vars.root).find(`mark[${_options.wordSelector}]`).each((i,elem)=>{
                const text = angular.element(elem).text();
                angular.element(elem).text(`\u00A0${text.trim()}\u00A0`);
            });
            this._deleteEmpty();
            this._updateFontStyle();
        };

        // helper function: move caret to special position in textnode
        this._moveCaretInText = (position, textNode) => {
            const caret = position;
            const range = $window.document.createRange();
            range.setStart(textNode, caret);
            range.collapse(true);
            const sel = $window.getSelection();
            sel.removeAllRanges();
            sel.addRange(range);
        };


        /** *********  main methods ********** */

        // colorize words
        // @params: colors map, callback function (optional)
        this.setColors = (colors, callback) => {
            this.inputComplete(() => {
                _vars.root.find(`mark[${_options.wordSelector}]`).each((i, elem) => {
                    const id = parseInt(angular.element(elem).attr(`${_options.wordSelector}`), 10);
                    const wordItem = _vars.words.get(id);
                    const color = colors.get(wordItem.word);
                    if (color) {
                        _colorizer._setWordColor(wordItem, color, angular.element(elem));
                    }
                });
                if (callback) {
                    callback();
                }
            });
        };

        // apply color to word
        // @param: color id, "word" node, new color
        this._setWordColor = (wordItem, color, node) => {
            wordItem.color.setColor(color);
            node.css({ "background-color": color.toString() });
            node.css({ color: calcFontColor(color) });
        };

        // subroutine: delete empty "words"
        this._deleteEmpty = () => {
            angular.element(_vars.root).find(`mark[${_options.wordSelector}]`).filter((i, elem) => {
                let ret;
                const content = angular.element(elem).html().toString().trim();                
                if (content) {
                    if (angular.element(elem).text().trim().length < 1 || angular.element(elem).text().trim() === '&nbsp;' || 
                        angular.element(elem).text().trim() === '' || angular.element(elem).find("br").length > 0
                        && !angular.element(elem).parent().is(_vars.root)) {
                        ret = true;
                    } else {
                        ret = false;
                    }
                } else {
                    ret = true;
                }
                return ret;
            }).remove();
            return this;
        };

        this.createWordMark = (id, txt, backgroundColor, fontColor) => `#%%%#;mark ${_options.wordSelector}=${id} style = ##%%%;background-color: ${backgroundColor}; color: ${fontColor};##%%%;#%%#%;\u00A0${txt}\u00A0#%#%%;mark#%%#%;`;

        // subroutine: handle changes in detected words
        this._inWordTags = () => {
            angular.element(_vars.root).find(`mark[${_options.wordSelector}]`).each((i, elem) => {
                const idString = angular.element(elem).attr(_options.wordSelector);
                const id = parseInt(idString, 10);
                const backgroundColor = angular.element(elem).css("background-color");
                const fontColor = angular.element(elem).css("color");
                const text = angular.element(elem).text();
                const format = XRegExp('^(?:\u00A0[\\pN\\pL]+\u00A0)$', 'i');
                const word = _vars.words.get(id);
                if (!text.match(format)) {
                    const newHtml = text.replace(XRegExp('[\\pN\\pL]+', 'gi'), (needle) => {
                        const color = colorFactory.getInstance(word.color);
                        const wordId = _vars.idCounter++;
                        _vars.words.set(wordId, { id: wordId, color, word: needle.toLowerCase() });
                        return this.createWordMark(wordId, needle, backgroundColor, fontColor);
                    });
                    _vars.wordsToRemove.add(id);
                    angular.element(elem).replaceWith(encodeSpecialChars(entities.encode(newHtml)));
                }
            });
            return this;
        };

        // handle (new) text outside from detected words
        // find all strings, which are not in (mark) tags and create new marks
        this._notInTags = () => {
            const reg = XRegExp('[\\pN\\pL]+', 'gi');
            angular.element(_vars.root).contents()
            .filter((i, item) => item.nodeType === 3)
            .each((i, item) => {
                const txt = item.nodeValue.trim().replace(/\s+/g, ' ');
                const html = txt.replace(reg, (needle) => {
                    const color = colorFactory.getInstance();
                    const wordId = _vars.idCounter++;
                    _vars.words.set(wordId, { id: wordId, color, word: needle.toLowerCase() });
                    return this.createWordMark(wordId, needle, null, null);
                });
                angular.element(item).replaceWith(encodeSpecialChars(entities.encode(html)));
            })
            .end();
            angular.element(_vars.root).find("br").replaceWith(" ");
            return this;
        };

        // set carte to the end of element
        // @params: element node
        this._setCaretPosition = (element) => {
            const range = $window.document.createRange();
            const sel = $window.getSelection();
            range.setStartAfter(element);
            range.setEndAfter(element);
            sel.removeAllRanges();
            sel.addRange(range);
        };

        // detect words in text
        // @param: callback function (optional)
        this.inputComplete = (callback) => {
            _vars.editMode = false;
            // unwrape, if text was wrapped in tags (for example by browser)
            angular.element(_vars.root).find("br").replaceWith(" ");
            angular.element(_vars.root).find(`:not(mark[${_options.wordSelector}])`).contents().unwrap();
            angular.element(_vars.root).find(`:not(mark[${_options.wordSelector}])`).remove();
            this._deleteEmpty()._inWordTags()._notInTags();
            angular.element(_vars.root).find(`:not(mark[${_options.wordSelector}])`).remove();
            angular.element(_vars.root).attr("contentEditable", false);
            const currentIds = new Set();
            angular.element(_vars.root).find(`mark[${_options.wordSelector}]`).each((i, elem) => {
                const idString = angular.element(elem).attr(_options.wordSelector);
                const id = parseInt(idString, 10);
                currentIds.add(id);
            });

            angular.element(_vars.root).find(`mark[${_options.wordSelector}]`).each((i, elem) => {
                if (elem.nextSibling && elem.nextSibling.nodeType !== 3) {
                    const node = $window.document.createTextNode('\u00A0');
                    elem.parentNode.insertBefore(node, elem.nextSibling);
                }
            });

            for (const id of _vars.words.keys()) {
                if (_vars.wordsToRemove.has(id) || !currentIds.has(id)) {
                    _vars.words.delete(id);
                }
            }
            _vars.wordsToRemove.clear();
            if (callback) {
                callback();
            }
        };

        // switch to edit mode and move caret to the end
        this.editMode = () => {
            _vars.editMode = true;
            angular.element(_vars.root).attr("contentEditable", true);
            const lastNodeType = (angular.element(_vars.root).contents().last()[0]) ? angular.element(_vars.root).contents().last()[0].nodeType : undefined;
            if (lastNodeType && lastNodeType !== 3) {
                const space = $window.document.createTextNode('\u00A0');
                angular.element(_vars.root).append(angular.element(space)); 
                _colorizer._setCaretPosition(angular.element(_vars.root).contents().last()[0]);
            } else if (lastNodeType) {
                angular.element(_vars.root).append(`<span ${_options.helperSelector}></span>`); 
                _colorizer._setCaretPosition(angular.element(_vars.root).find(`span[${_options.helperSelector}]`)[0]);
                $timeout(() => {
                    angular.element(_vars.root).find(`span[${_options.helperSelector}]`).remove(); 
                }, 0);
            }
            angular.element(_vars.root)[0].focus();
        };

        // set one color
        // @params: color object, new color, callback function (optional)
        this.setColor = (word, newColor, callback) => {
            if (!_vars.editMode) {
                if (word) {
                    const node = angular.element(_vars.root).find(`mark[${_options.wordSelector}=${word.id}]`);
                    if (node && node.length > 0) {
                        const savedWord = _vars.words.get(word.id);
                        savedWord.color.setColor(newColor);
                        angular.element(node).css({ "background-color": savedWord.color.toString(), color: calcFontColor(savedWord.color) });
                        angular.element(node).attr(_options.colorSelector, savedWord.color.toString());
                        angular.element(node).attr(_options.weightSelector, savedWord.color.weight);
                        $timeout(() => {
                            savedWord.node = node;
                            if (callback) {
                                callback();
                            }
                        }, 0);
                    }
                }
            } else if (callback) {
                callback();
            }
        };

        // return all colors as map
        this.getAllColors = () => _vars.words;

        // reset one color (decolorize word)
        // @params: color object, callback function (optional)
        this.resetColor = (word, callback) => {
            if (!_vars.editMode) {
                if (word) {
                    const node = angular.element(_vars.root).find(`mark[${_options.wordSelector}=${word.id}]`);
                    if (node && node.length > 0) {
                        angular.element(node).removeAttr("style");
                        angular.element(node).removeAttr(_options.weightSelector);
                        angular.element(node).removeAttr(_options.colorSelector);
                        const savedWord = _vars.words.get(word.id);
                        if (savedWord) {
                            savedWord.color.reset();
                        }
                    }
                }
            }
            callback(this);
        };

        this.resetAllColors = (callback) => {
            angular.element(_vars.root)
                .find(`mark[${_options.wordSelector}]`)
                .removeAttr("style")
                .removeAttr(_options.weightSelector)
                .removeAttr(_options.colorSelector);
                for (const word of _vars.words.values()) {
                    word.color.reset();
                }
            if (callback) {
                callback();
            }
        };

        // event handler for "click" events in the widget
        // event delegation -> handles clicks on words in editor
        // @params: event object
        this.selectWord = ($event) => {
            const target = $event.target;
            if (target && angular.element(target)[0].hasAttribute(_options.wordSelector)){
                if (!_vars.editMode) {
                    $event.stopPropagation();
                    const id = angular.element(target).attr(_options.wordSelector);
                    const savedWord = _vars.words.get(parseInt(id, 10));
                    this.onWordselect({ word: savedWord });
                } else {
                    // for better handling in Chrome
                    const textNode = angular.element(target).contents().first()[0];
                    const anchorNode = $window.getSelection().anchorNode;
                    if (anchorNode && !textNode.isSameNode(anchorNode)) {
                        const nativeText = angular.element(target).text();
                        if (nativeText[0].trim().length > 0) {
                            const spaceText = $window.document.createTextNode(`\u00A0${textNode.textContent.trim()}\u00A0`);
                            textNode.parentNode.replaceChild(spaceText, textNode);
                            this._moveCaretInText(1, spaceText);
                        }
                    }
                }
            }
        };

        // delete all color assignments and empty colors map
        // @params: callback function (optional)
        this.clear = (callback) => {
            _vars.words.clear();
            angular.element(_vars.root).attr("contentEditable", true);
            angular.element(_vars.root).empty();
            angular.element(_vars.root)[0].focus();
            callback();
        };
    }],

    bindings: {
        control: '<',
        onWordselect: '&',
    },
});

export default 'text-colorizer';



