export default class SettingsCtrl {

    constructor($scope, $appSettings) {
        const controller = this;
        this.settings = $appSettings;
        let settingsOldState = Object.values(this.settings.serialize()).join();
        this.slider = {
            value: 50,
            options: {
                floor: 0,
                ceil: 100,
                step: 1,
                onChange: () => {},
                onEnd: () => {
                    controller.settings.similarityLevel = controller.similarityLevel;
                },
                hidePointerLabels: true,
                hideLimitLabels: true,
                disabled: true,
            },
        };
        this.similarityLevel = $appSettings.similarityLevel;

        this.$doCheck = () => {
            controller.slider.options.disabled = (controller.settings.searchMode === "regular");
            const state = Object.values(controller.settings.serialize()).join();
            if (state !== settingsOldState) {
                settingsOldState = state;
                controller.settings.save(() => {
                    controller.similarityLevel = $appSettings.similarityLevel;
                    $scope.$apply();
                }, (e) => {
                    console.log("SettingsCtrl: Error occured");
                    console.log(e);
                });
            }
        };

        this.$onInit = () => {
            $appSettings.load(() => {
                this.similarityLevel = $appSettings.similarityLevel;
                $scope.$apply();
            }, (e) => {
                console.log("SettingsCtrl: Error occured");
                console.log(e);
            });
        };
    }
}




