import 'chart.js';
import chart from 'angular-chart.js';
import slider from 'angularjs-slider';
import './scss/color-adjuster.scss';
import template from './template/color-adjuster.html';

angular.module('color-adjuster', [slider, chart])
.component('colorAdjuster', {
    template,
    controller: ["$scope", "$element", "$attrs", "$timeout", "colorUtils", "colorFactory", "eventFilter",
    function Controller($scope, $element, $attrs, $timeout, colorUtils, colorFactory, eventFilter) {
        const adjuster = this;

        this.toggle = () => {
            $timeout(() => {
                $scope.$broadcast('rzSliderForceRender');
            }, 0, false);
        };

        this.getS = () => this.textColors.sPercentage < 100 ? `-${Math.abs(adjuster.textColors.sPercentage - 100)}` : `+${Math.abs(adjuster.textColors.sPercentage - 100)}`;
        this.getL = () => this.textColors.lPercentage < 100 ? `-${Math.abs(adjuster.textColors.lPercentage - 100)}` : `+${Math.abs(adjuster.textColors.lPercentage - 100)}`;

        const colorsChainged = () => {
            const tempColors = new Map();
            const wei = [];
            const cols = [];

            for (const [key, color] of this.textColors.adjustedColorOccurence.entries()) {
                const rgbHex = colorUtils.toHexString(key);
                const tempColor = tempColors.get(rgbHex);
                if (tempColor) {
                    tempColor.weight += color.weight;
                } else {
                    const col = colorFactory.getInstance(color);
                    tempColors.set(rgbHex, col);
                }
            }

            for (const [key, value] of tempColors) {
                cols.push(key);
                wei.push(value.weight);
            }

            adjuster.data = wei;
            adjuster.colors = cols;

            $timeout(() => {
                $scope.$apply();
            }, 0, false);
        };

        this.labels = ['huhu','lol'];
        this.data = [];
        this.colors = [];

        const timeWindow = 1000 / 30;
        const filter = eventFilter(timeWindow, () => {
            adjuster.textColors.calculateAdjustedColors();
            colorsChainged();
        });

        this.options = {
            tooltips: {
                enabled: false,
            },
            animation: {
                animateRotate: false,
            },
            events: [],
        };

        this.slider = {
            value: 50,
            options: {
                floor: 0,
                ceil: 200,
                step: 1,
                onChange: () => {
                    filter.event();
                },
                onEnd: () => {
                    adjuster.textColors.calculateAdjustedColors();
                    colorsChainged();
                },
                hidePointerLabels: true,
                hideLimitLabels: true,
                disabled: false,
            },
        };

        $scope.$on("colorsChainged", colorsChainged);

        this.$onInit = () => {
            colorsChainged();
        };
    }],

    bindings: {
        textColors: '<',
    },
});


export default 'color-adjuster';



