import slider from 'angularjs-slider';
import './scss/color-toolbar.scss';
import template from './template/color-toolbar.html';

angular.module('color-toolbar', [slider])
.component('colorToolbar', {
    template,
    controller: ["$rootScope", "$scope", "$element", "$attrs",
    "$timeout", "$window", "colorFactory", "eventFilter",
    function Controller($rootScope, $scope, $element, $attrs,
        $timeout, $window, colorFactory, eventFilter) {
        const toolbar = this;
        this.id = $attrs["toolbarId"];
        this.colors = [];

        for (let i = 0; i < 360; i += 30) {
            const color = colorFactory.getInstance({ h: i, s: 50, l: 50 });
            this.colors.push(color);
        }
        const black = colorFactory.getInstance({ h: 0, s: 0, l: 0 }).setAchromatic();
        const white = colorFactory.getInstance({ h: 0, s: 0, l: 100 }).setAchromatic();
        this.colors.push(black);
        this.colors.push(white);
        this.color = colorFactory.getInstance();

        const timeWindow = 1000 / 30;
        const filter = eventFilter(timeWindow, () => {
            toolbar.colorModifiedHandler({ color: toolbar.color });
        });

        this.slider = {
            value: 50,
            options: {
                floor: 0,
                ceil: 100,
                step: 1,
                onChange() {
                    filter.event();
                },
                onEnd() {
                    toolbar.colorModifiedHandler({ color: toolbar.color });
                },
                hidePointerLabels: true,
                hideLimitLabels: true,
                disabled: true,
            },
        };

        this.increaseWeight = () => {
            if (this.color && this.color.isSet() && this.color.weight < 100) {
                toolbar.color.weight ++;
                this.colorModifiedHandler({ color: toolbar.color });
            }
        };

        this.decreaseWeight = () => {
            if (this.color && this.color.isSet() && this.color.weight > 1) {
                toolbar.color.weight --;
                this.colorModifiedHandler({ color: toolbar.color });
            }
        };

        this.endSlide = () => {
            this.colorModifiedHandler({ color: toolbar.color });
        };

        this.selectColor = ($event) => {
            const target = $event.target;
            if (target.hasAttribute("data-toolbar-color") || target.parentElement.hasAttribute("data-toolbar-color")) {
                $event.stopPropagation();
                const colorElement = target.hasAttribute("data-toolbar-color") ? target : target.parentElement;
                const colorIndex = angular.element(colorElement).attr("data-toolbar-color");
                if (colorIndex && colorIndex !== "null") {
                    const color = colorFactory.getInstance(this.colors[colorIndex]);
                    this.changeColor(color);
                    this.slider.options.disabled = this.color.isAchromatic();
                } else {
                    this.setInitialColor();
                }
                this.colorModifiedHandler({ color: toolbar.color });
            }
        };

        this.changeColor = (color) => {
            color.weight = this.color.weight;
            if (!color.isAchromatic() && !this.color.isAchromatic()
                && this.color.s && this.color.l) {
                color.s = this.color.s;
                color.l = this.color.l;
            }
            this.color = color;
        };

        this.setCurrentColor = (color) => {
            if (color && color.isSet()) {
                this.color = color;
                this.slider.options.disabled = this.color.isAchromatic();
            } else {
                this.setInitialColor();
            }
        };

        this.setInitialColor = () => {
            this.color = colorFactory.getInstance();
            this.slider.options.disabled = true;
        };

        this.close = () => {
            $element.addClass("hide");
        };

        this.open = () => {
            $element.removeClass("hide");
            $timeout(() => {
                $scope.$broadcast('rzSliderForceRender');
            }, 0);
        };

        // public api
        const api = {
            setColor(color) {
                toolbar.setCurrentColor(color);
            },
            close() {
                toolbar.close();
            },
            open() {
                toolbar.open();
            },
        };

        $scope.$on("color-toolbar", (event, callback) => {
            if (typeof callback === "function") {
                callback(api);
            }
        });

        this.$onInit = () => {
            if (this.closed === true) {
                this.close();
            }
            angular.element($window).bind('resize orientationchange', () => {
                $rootScope.$broadcast('rzSliderForceRender');
            });
        };
    }],

    bindings: {
        colorModifiedHandler: '&',
        closed: '<',
    },
});

export default 'color-toolbar';




